/* Forgot Password Page Styles */
.forgot-password-page {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #f3f4f6; /* Light gray background */
  }
  
  .form-container {
    background-color: #ffffff;
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 400px;
  }
  
  h1 {
    font-size: 24px;
    margin-bottom: 20px;
    text-align: center;
    font-weight: bold;
    color: #333;
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  input {
    width: 100%;
    padding: 12px 16px;
    margin: 5px 0;
    border-radius: 8px;
    border: 1px solid #ddd;
    font-size: 14px;
    outline: none;
    transition: border-color 0.3s ease;
  }
  
  input:focus {
    border-color: #3498db; /* Custom blue color */
  }
  
  .submit-button {
    width: 100%;
    padding: 14px;
    background-color: #3498db; /* Custom blue color */
    color: white;
    font-size: 16px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .submit-button:hover {
    background-color: #2980b9; /* Darker blue for hover effect */
  }
  
  .submit-button:disabled {
    background-color: #bbb;
    cursor: not-allowed;
  }
  
  .error-message {
    color: red;
    font-size: 14px;
    margin-bottom: 20px;
  }
  
  .form-footer {
    margin-top: 20px;
    text-align: center;
  }
  
  .form-footer a {
    color: #3498db; /* Custom blue color for links */
    font-size: 14px;
    text-decoration: none;
    margin: 0 5px;
  }
  
  .form-footer a:hover {
    text-decoration: underline;
  }
  